<template>
  <div class="goods-classify">
    <back-header title="商品分类" />
    <div class="body-wrap">
      <ul class="tabs">
        <li @click="changeTab(item)" v-for="item in list" :key="item.id" :class="{active: item.id === activeTab.id}">
          <i></i>
          <span>{{ item.title }}</span>
        </li>
      </ul>
      <div class="content">
        <p @click="changeChild(item)" :class="{active: item.id === activeChild.id}" v-for="item in activeTab.subcates" :key="item.id">{{ item.title }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import { post } from '../../common/js/http'
import { setItem } from '../../common/js/storage'

export default {
  name: 'goods-classify',
  components: {
    BackHeader
  },
  data() {
    return {
      list: [],
      activeTab: {},
      activeChild: {}
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    getList() {
      const loading = this.$skeLoading('.goods-classify')
      post('/shop/goods/getCategoryList')
        .then(({ data: { data } }) => {
          this.list = data
          this.activeTab = data[0]
          loading.close()
        })
        .catch(() => {
          loading.close()
        })
    },
    changeTab(item) {
      this.activeTab = item
    },
    changeChild(item) {
      this.activeChild = item
      setItem('selectClassify', item)
      this.$router.goBack()
    }
  }
}
</script>

